.location__text {
  width: 140px;

  line-height: 20px;
  font-size: 16px;
  text-align: left;
}

.location__address {
  line-height: 20px;

  color: $text-color;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  text-transform: none;
}

@media (min-width: $tablet-width) {
  .location__text {
    padding-left: 42px;

    width: 200px;

    line-height: 25px;
    font-size: 20px;
  }

  .location__address {
    padding-right: 42px;

    width: 200px;

    line-height: 20px;
    font-size: 16px;
    text-align: right;
  }
}


@media (min-width: $desktop-width) {
  .location__text,
  .location__address {
    padding: 0;
    width: 170px;
  }

  .location__text {
    text-transform: uppercase;
  }

  .location__address {
    text-align: left;
  }
}
