.page-footer {
  background-color: $main-gray;
}

.page-footer__body {
  background-image: linear-gradient(to bottom, $main-white 50%, $main-gray 50%,);
}

.page-footer__location {
  display: flex;
  align-items: center;

  justify-content: space-around;

  min-height: 108px;

  background-color: $main-white;
}

.page-footer__logo {
  display: flex;
  align-items: center;
  justify-content: center;

  min-height: 90px;

  border-bottom: 1px solid #d9d9d9;

  img {
    width: 128px;
    height: 24px;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-footer__map {
  iframe {
    width: 100%;
    height: 362px;

    border: 0;
  }
}

.page-footer__additional-info {
  min-height: 236px;

  background-color: $main-gray;
}

.page-footer__social-list {
  display: flex;
  align-items: center;
  justify-content: center;

  min-height: 63px;

  border-bottom: 1px solid #d9d9d9;

  li {
    margin-right: 20px;

    &:last-child {
      margin: 0;
    }
  }
}

.page-footer__social-link {
  display: flex;
  justify-content: center;

  width: 20px;
  height: 20px;
}

.page-footer__copyright {
  display: flex;
  align-items: center;
  justify-content: space-between;

  height: 80px;

  font-family: "Arial", sans-serif;
  font-size: 16px;
  text-transform: capitalize;

  span {
    text-transform: uppercase;
  }
}

.page-footer__copyright-link {
  display: flex;
  align-items: center;

  width: 100%;

  color: #666666;

  svg {
    margin-left: auto;

    width: 27px;
    height: 34px;
  }
}

@media (min-width: $tablet-width) {
  .page-footer__location {
    justify-content: space-between;

    height: 190px;

    background-color: $main-white;
  }

  .page-footer__additional-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .page-footer__social-list {
    width: 324px;

    border: 0;
  }

  .page-footer__logo,
  .page-footer__copyright {
    width: 162px;

    border: 0;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__body {
    position: relative;

    background-color: $main-gray;
  }

  .page-footer__location {
    position: absolute;
    top: 100px;
    left: 350px;

    width: 570px;
    min-height: 201px;

    z-index: 5;

    background-color: rgb(255, 255, 255);
  }

  .page-footer__additional-info {
    min-height: 150px;
  }

  .page-footer__map {
    iframe {
      height: 400px;
    }
  }
}
